.messages {
    display: flex;
    margin-left: 70px;
    margin-top: 30px;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
}

.dialogs {
    display: flex;
    flex-direction: column;
    width: 300px;
    background-color: #ffffff;
}

.dialogs .active {
    color: white;
}

.chats {
    border-top: 1px solid #e2e9e6;
    width: 500px;
    min-height: 500px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.noMessages {
    justify-self: center;
    align-self: center;
    margin-bottom: 200px;
    font-size: 20px;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background-color: #f4f4f4;
}

.form button {
    background-color: #088dcd;
    color: #ffffff;
    border: none;
    font-size: 13px;
    font-weight: 500;
    padding: 4px 20px;
    border-radius: 4px;
    outline: none;
}

.form button:hover {
    cursor: pointer;
    background-color: #086393;
}

.form button:disabled {
    opacity: 0.5;
}

.select {
    font-size: 20px;
    margin-left: 200px;
    margin-top: 15px;
}